import './App.css';
import { Box, Button, createTheme, Stack, ThemeProvider } from '@mui/material';
import { Route, Routes } from 'react-router-dom';
import About from './components/About';
import Projects from './components/Projects';
import { useState } from 'react';

const theme = createTheme({
  palette: {
    mode: 'dark'
  }
})

type AppVisibility = {
  showAbout: boolean,
  showProjects: boolean,
}

const App = () => {
  const [appVisibility, setappVisibility] = useState<AppVisibility>({
    showAbout: true,
    showProjects: false
  });

  const showAbout = () => {
    setappVisibility({
      showAbout: true,
      showProjects: false
    })
  }

  const showProjects = () => {
    setappVisibility({
      showAbout: false,
      showProjects: true
    })
  }
  return <>
    <ThemeProvider theme={theme}>
      <Box width={'100%'} marginTop={'1rem'} marginLeft={'1rem'}>
        <Stack direction={'row'} spacing={2}>
          <Button variant={appVisibility.showAbout ? 'contained' : 'outlined'} onClick={showAbout} >About</Button>
          <Button variant={appVisibility.showProjects ? 'contained' : 'outlined'} onClick={showProjects}>Projects</Button>
        </Stack>
        <Box width={'100%'} marginTop={'1rem'} marginLeft={'1rem'}>
          <>
            {appVisibility.showAbout && <About />}
            {appVisibility.showProjects && <Projects />}
          </>
        </Box>
      </Box>


    </ThemeProvider>
  </>
}

export default App;
